@import url(https://fonts.googleapis.com/css2?family=Just+Another+Hand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background: rgb(255, 255, 255);
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(238, 238, 238, 1) 100%
  );
}

#first-image {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.scrolling-wrapper {
  width: 50%;
  border: 1px solid #ddd;
  display: -webkit-flex;
  display: flex;
  overflow: auto;
}

.scrolling-wrapper::-webkit-scrollbar {
  width: 0;
}

.scrolling-wrapper .item {
  line-height: 110px;
  text-align: center;
  background-color: #ddd;
  margin-right: 2px;
}

#carousel-container {
  padding: 100px 50px;
}

#title-text {
  font-family: "Just Another Hand", cursive;
  font-size: 75px;
  letter-spacing: 10px;
  text-align: center;
  text-shadow: 1px 1px 2px rgb(255, 255, 255), 0 0 1em rgb(194, 141, 81),
    0 0 0.2em rgb(134, 92, 44);
}

.rec.rec-arrow {
  background-color: rgb(102, 89, 66);
  border-radius: 0;
}

.rec.rec-arrow:hover {
  border-radius: 50%;
}

.rec.rec-arrow:disabled {
  visibility: hidden;
}

.rec-carousel-item:focus {
  outline: none;
  box-shadow: inset 0 0 1px 1px lightgrey;
}

.rec.rec-dot {
  box-shadow: 0 0 1px 3px rgb(180, 145, 105);
}

.rec.rec-dot_active {
  background-color: rgb(134, 92, 44);
}

.card-wrapper {
  width: 15rem;
  min-height: 30rem;
  max-height: 30rem;
  padding: 20px;
  margin: 10px;
  border-radius: 25px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
  background: rgb(255, 255, 255);
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(238, 238, 238, 1) 100%
  );
  padding-bottom: 5rem;
}

#carousel-card-title {
  height: 50px;
  text-align: center;
}

.body-text {
  font-family: "Yanone Kaffeesatz", sans-serif;
  line-height: 1.5;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 8; /* number of lines to show */
  -webkit-box-orient: vertical;
}

.preview-image {
  width: 15rem;
  height: 10rem;
  border-radius: 25px;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

.card-wrapper .recipe-button {
  position: absolute;
  bottom: 2rem;
}

.button {
  font-family: "Hoefler Text", Georgia, "Times New Roman", serif;
  background: rgb(102, 89, 66);
  border: 1px solid;
  border-color: rgb(126, 121, 110);
  border-radius: 2rem;
  padding: 0.5rem;
  box-sizing: border-box;
  font-size: 1rem;
  color: white;
  cursor: pointer;
}

.button:hover {
  background: rgba(162, 155, 141, 1);
}

#recipe-title {
  font-family: "Dancing Script", cursive;
  font-size: 50px;
  letter-spacing: 10px;
  text-align: center;
  text-shadow: 1px 1px 2px rgb(255, 255, 255), 0 0 1em rgb(194, 141, 81),
    0 0 0.2em rgb(134, 92, 44);
}

#image-preview {
  height: 650px;
  width: 650px;
  margin: 20px 20px;
  border-radius: 50px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

#details-container {
  font-family: "Yanone Kaffeesatz", sans-serif;
  font-size: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  overflow: auto;
}

#recipe-information-container {
  font-family: "Yanone Kaffeesatz", sans-serif;
  font-size: 20px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 500px;
